
.md-render-wrapper {
  max-width: 740px;
  font-size: 14px;
  padding: .5rem;
  line-height: 1.5;
  margin: 1rem auto;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #444;
}
h1,
h2,
h3,
h4 {
  margin-top: 1em;
  margin-bottom: .5em;
  font-weight: 400;
}
h1 {
  font-size: 2rem;
  padding-bottom: .5rem;
  border-bottom: solid 1px #ddd;
  margin-bottom: 1rem;
}
h2 {
  font-size: 1.8rem;
}
h3 {
  font-size: 1.6rem;
}
h4 {
  font-size: 1.2rem;
}
h5 {
  font-size: 1rem;
}
h5 {
  font-size: .8rem;
}
strong {
  color: #dd4b39;
}
a {
  color: #42A5F5;
  text-decoration: none;
  font-weight: normal;
  margin-right: .5rem;
  border-radius: 2px;
  padding: 2px;
}
a:after {
  content: '>>';
}
a:hover,
a:focus,
a:active {
  background-color: #42A5F5;
  color: white;
}
hr {
  height: 0;
  margin: 15px 0;
  overflow: hidden;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #ddd;
}
img {
  display: block;
  margin: 1rem auto;
  max-width: 95%;
  border-radius: 0.3em;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 1px 4px;
          box-shadow: rgba(0, 0, 0, 0.3) 0 1px 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
video {
  display: block;
  margin: 1rem auto;
  max-width: 95%;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 1px 4px;
          box-shadow: rgba(0, 0, 0, 0.3) 0 1px 4px;
}
code {
  display: inline-block;
  white-space: no-wrap;
  background: #eee;
  font-size: 1rem;
  line-height: 1.2em;
  color: #00897b;
  border-radius: 0.3em;
  padding: 0 .3em;
  margin: -1px 4px;
}



